import { useState } from "react";
import { supabase } from "lib/supabase";
import { Icon } from "@iconify/react";
import toast from "react-hot-toast";

export default function JobListings({ mode, jobs, onJobDeleted }) {
    const [viewMode, setViewMode] = useState("cards");

    const handleDeleteJob = async (jobId) => {
        const { error } = await supabase.from("job_openings").delete().eq("id", jobId);
        if (error) {
            console.error("Error deleting job:", error);
            toast.error("Failed to delete job opening.");
        } else {
            toast.success("Job opening deleted successfully!", { icon: "✅" });
            onJobDeleted();
        }
    };

    return (
        <div className={`p-6 rounded-lg shadow-lg ${mode === "dark" ? "bg-gray-800" : "bg-white"}`}>
            <div className="flex justify-between items-center mb-4">
                <div className="flex items-center gap-2">
                    <Icon icon="mdi:briefcase" className="w-6 h-6 text-[#f05d23]" />
                    <h3 className={`text-xl font-bold ${mode === "dark" ? "text-white" : "text-[#231812]"}`}>
                        Current Job Openings
                    </h3>
                </div>
                <button
                    onClick={() => setViewMode(viewMode === "cards" ? "table" : "cards")}
                    className={`px-3 py-1 rounded-lg flex items-center gap-2 ${
                        mode === "dark" ? "bg-gray-700 text-white hover:bg-gray-600" : "bg-gray-200 text-[#231812] hover:bg-gray-300"
                    }`}
                >
                    <Icon icon={viewMode === "cards" ? "mdi:table" : "mdi:view-dashboard"} width={20} height={20} />
                    {viewMode === "cards" ? "Table View" : "Card View"}
                </button>
            </div>
            {jobs.length === 0 ? (
                <p className={`italic text-center ${mode === "dark" ? "text-gray-400" : "text-gray-500"}`}>
                    No job openings yet.
                </p>
            ) : viewMode === "cards" ? (
                <div className="grid gap-6 md:grid-cols-2">
                    {jobs.map((job) => (
                        <div
                            key={job.id}
                            className={`p-4 rounded-lg shadow-md hover:shadow-lg transition duration-200 border-t-2 border-[#f05d23] ${
                                mode === "dark" ? "bg-gray-700 text-white" : "bg-white text-[#231812]"
                            } ${job.is_expired ? "opacity-50" : ""}`}
                        >
                            <h4 className="text-lg font-semibold mb-2">{job.title}</h4>
                            {job.description && (
                                <div
                                    className="mt-1 prose max-w-none mb-2 text-sm"
                                    dangerouslySetInnerHTML={{ __html: job.description }}
                                />
                            )}
                            {job.file_url && (
                                <a
                                    href={job.file_url}
                                    className="text-[#f05d23] underline hover:text-[#d94f1e] mt-1 inline-block text-sm"
                                    target="_blank"
                                    rel="noopener noreferrer"
                                >
                                    View File
                                </a>
                            )}
                            <p className={`text-sm mt-2 ${mode === "dark" ? "text-gray-400" : "text-gray-500"}`}>
                                Expires: {new Date(job.expires_on).toLocaleDateString()} {job.is_expired && "(Expired)"}
                            </p>
                            <button
                                onClick={() => handleDeleteJob(job.id)}
                                className="mt-3 px-3 py-1 bg-red-500 text-white rounded-lg hover:bg-red-600 transition duration-200 flex items-center gap-2"
                            >
                                <Icon icon="mdi:trash-can" width={20} height={20} />
                                Delete
                            </button>
                        </div>
                    ))}
                </div>
            ) : (
                <div className="overflow-x-auto">
                    <table
                        className={`w-full rounded-lg shadow-md ${
                            mode === "dark" ? "bg-gray-700 text-white" : "bg-white text-[#231812]"
                        }`}
                    >
                        <thead>
                        <tr className={`${mode === "dark" ? "bg-gray-600" : "bg-gray-100"}`}>
                            <th className="p-3 text-left">Title</th>
                            <th className="p-3 text-left">Description</th>
                            <th className="p-3 text-left">File</th>
                            <th className="p-3 text-left">Expires</th>
                            <th className="p-3 text-left">Actions</th>
                        </tr>
                        </thead>
                        <tbody>
                        {jobs.map((job) => (
                            <tr
                                key={job.id}
                                className={`border-b ${job.is_expired ? "opacity-50" : ""} ${
                                    mode === "dark" ? "border-gray-600" : "border-gray-200"
                                }`}
                            >
                                <td className="p-3">{job.title}</td>
                                <td className="p-3">
                                    {job.description ? (
                                        <div
                                            className="prose max-w-none text-sm"
                                            dangerouslySetInnerHTML={{ __html: job.description }}
                                        />
                                    ) : (
                                        "N/A"
                                    )}
                                </td>
                                <td className="p-3">
                                    {job.file_url ? (
                                        <a
                                            href={job.file_url}
                                            className="text-[#f05d23] underline hover:text-[#d94f1e]"
                                            target="_blank"
                                            rel="noopener noreferrer"
                                        >
                                            View
                                        </a>
                                    ) : (
                                        "N/A"
                                    )}
                                </td>
                                <td className="p-3">
                                    {new Date(job.expires_on).toLocaleDateString()} {job.is_expired && "(Expired)"}
                                </td>
                                <td className="p-3">
                                    <button
                                        onClick={() => handleDeleteJob(job.id)}
                                        className="px-2 py-1 bg-red-500 text-white rounded-lg hover:bg-red-600 transition duration-200 flex items-center gap-2"
                                    >
                                        <Icon icon="mdi:trash-can" width={16} height={16} />
                                        Delete
                                    </button>
                                </td>
                            </tr>
                        ))}
                        </tbody>
                    </table>
                </div>
            )}
        </div>
    );
}