import { useState } from "react";
import { supabase } from "lib/supabase";
import { Icon } from "@iconify/react";
import toast from "react-hot-toast";
import ReactQuill from "react-quill"; // Quill Editor
import "react-quill/dist/quill.snow.css"; // Import Quill's default styles

const theme = {
    paragraph: `prose max-w-none p-4 border rounded-lg focus:outline-none focus:ring-2 focus:ring-[#f05d23] min-h-[200px]`,
};

export default function JobForm({ mode, onJobAdded }) {
    const [title, setTitle] = useState("");
    const [description, setDescription] = useState("");
    const [file, setFile] = useState(null);
    const [expiresOn, setExpiresOn] = useState("");

    const handleChangeDescription = (value) => {
        setDescription(value);
    };

    const handleSubmitJob = async (e) => {
        e.preventDefault();
        let fileUrl = file ? "https://drive.google.com/example" : null;

        const jobData = {
            title,
            description: description || null,
            file_url: fileUrl || null,
            expires_on: expiresOn,
            is_expired: false,
        };

        const { error } = await supabase.from("job_openings").insert([jobData]);
        if (error) {
            toast.error("Failed to submit job opening.");
        } else {
            toast.success("Job opening added successfully!", { icon: "✅" });
            setTitle("");
            setDescription("");
            setFile(null);
            setExpiresOn("");
            onJobAdded();
        }
    };

    const handleFileChange = (e) => setFile(e.target.files[0]);

    return (
        <div className={`p-6 rounded-lg shadow-lg mb-8 border-t-4 border-[#f05d23] ${mode === "dark" ? "bg-gray-800" : "bg-white"}`}>
            <div className="flex items-center mb-4">
                <Icon icon="mdi:briefcase-plus" className="w-6 h-6 text-[#f05d23] mr-2" />
                <h3 className={`text-xl font-bold ${mode === "dark" ? "text-white" : "text-[#231812]"}`}>Add New Job Opening</h3>
            </div>
            <form onSubmit={handleSubmitJob} className="space-y-6">
                <div>
                    <label className={`block text-sm font-medium mb-2 ${mode === "dark" ? "text-gray-300" : "text-[#231812]"}`}>
                        Job Title <span className="text-red-500">*</span>
                    </label>
                    <div className="relative">
                        <Icon icon="mdi:format-title" className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-[#f05d23]" />
                        <input
                            type="text"
                            value={title}
                            onChange={(e) => setTitle(e.target.value)}
                            className={`w-full pl-10 pr-4 py-3 border rounded-lg focus:outline-none focus:ring-2 focus:ring-[#f05d23] ${mode === "dark" ? "bg-gray-700 border-gray-600 text-white" : "bg-gray-50 border-gray-300 text-[#231812]"}`}
                            placeholder="e.g., Software Engineer"
                            required
                        />
                    </div>
                </div>
                <div>
                    <label className={`block text-sm font-medium mb-2 ${mode === "dark" ? "text-gray-300" : "text-[#231812]"}`}>
                        Description (Optional)
                    </label>
                    <ReactQuill
                        value={description}
                        onChange={handleChangeDescription}
                        placeholder="Enter job description..."
                        className={`min-h-[200px] ${mode === "dark" ? "bg-gray-700 text-white" : "bg-white"}`}
                    />
                </div>
                <div>
                    <label className={`block text-sm font-medium mb-2 ${mode === "dark" ? "text-gray-300" : "text-[#231812]"}`}>
                        Upload File (Optional)
                    </label>
                    <div className="relative">
                        <Icon icon="mdi:file-upload" className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-[#f05d23]" />
                        <input
                            type="file"
                            accept=".pdf,.docx"
                            onChange={handleFileChange}
                            className={`w-full pl-10 pr-4 py-3 border rounded-lg ${mode === "dark" ? "bg-gray-700 border-gray-600 text-gray-300" : "bg-gray-50 border-gray-300 text-[#231812]"}`}
                        />
                    </div>
                </div>
                <div>
                    <label className={`block text-sm font-medium mb-2 ${mode === "dark" ? "text-gray-300" : "text-[#231812]"}`}>
                        Expires On <span className="text-red-500">*</span>
                    </label>
                    <div className="relative">
                        <Icon icon="mdi:calendar" className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-[#f05d23]" />
                        <input
                            type="date"
                            value={expiresOn}
                            onChange={(e) => setExpiresOn(e.target.value)}
                            className={`w-full pl-10 pr-4 py-3 border rounded-lg focus:outline-none focus:ring-2 focus:ring-[#f05d23] ${mode === "dark" ? "bg-gray-700 border-gray-600 text-white" : "bg-gray-50 border-gray-300 text-[#231812]"}`}
                            required
                        />
                    </div>
                </div>
                <button
                    type="submit"
                    className="w-full py-3 bg-[#f05d23] text-white rounded-lg hover:bg-[#d94f1e] transition duration-200 shadow-md flex items-center justify-center gap-2"
                >
                    <Icon icon="mdi:plus" width={20} height={20} />
                    Add Job Opening
                </button>
            </form>
        </div>
    );
}
